@font-face {
  font-family: SulphurPoint;
  src: url("/src/Fonts/SulphurPoint-Regular.ttf");
}

* {
  margin: 0;
}
a {
  color: inherit;
  font-family: SulphurPoint;
  text-decoration: none;
}
p {
  margin: 10px 0;
  line-height: 1.857em;
  font-size: 2vh;
  font-weight: 500;
  font-family: sans-serif;
}
h1 {
  font-family: SulphurPoint;
}
.header {
  height: 7vh;
  display: flex;
  align-items: center;
}
.header-logo-link {
  height: 5vh;
  float: left;
  margin-left: 35px;
  margin-right: auto;
}
.main-nav-wrapper {
  margin-top: 5px;
  margin-right: 35px;
  display: flex;
  align-items: center;
  font-size: 3.5vh;
}
.social-item {
  margin-left: 25px;
}
.nav-item:hover {
  color: darkgray;
  cursor: pointer;
}
.main-logo {
  height: 5vh;
}
.grey-hover:hover {
  color: grey;
}
.footer {
  background-color: black;
  padding: 6vh;
  bottom: 0;
}

.footer a {
  color: white;
}

.project-wrapper {
  min-height: 35vw;
  position: relative;
}
.modal-container {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.95);
  z-index: 100;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  color: white;
}
.modal-exit-icon {
  position: relative;
  font-size: 3vw;
  padding-top: 2.5%;
  padding-right: 5%;
  left: 90%;
}
.modal-exit-icon:hover {
  color: darkgray;
  cursor: pointer;
}
.modal-navigation-container {
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 5vw;
  font-weight: 800;
  height: 30%;

  margin-left: -150px;
  margin-top: -150px;
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: space-between;
}

@media screen and (max-width: 1025px) {
  .modal-exit-icon {
    font-size: 30px;
  }

  .main-logo {
    height: 40px;
  }
  .header-logo-link {
    height: 40px;
  }
  .header {
    height: 60px;
    display: flex;
    align-items: center;
  }
  .main-nav-wrapper {
    font-size: 25px;
  }
  .modal-navigation-container {
    font-size: 80px;
    height: 300px;
    width: 300px;
  }
  .project-wrapper {
    min-height: 45vw;
  }
}
